import React from 'react';
import { render } from 'react-dom';
import App from './App';
import LoginApp from './loginApp'
import SignUp from './registerApp'
import Recovery from './components/Auth/RecoverPassword'
import './index.css'

const loginTag = document.getElementById('login')
const signUpTag = document.getElementById('signup')
const productsTag = document.getElementById('root')
const recoveryTag = document.getElementById('recovery')


if (loginTag) {
    render(<LoginApp />, loginTag);
}
if (productsTag) {
    render(<App />, productsTag);
}
if (signUpTag) {
    render(<SignUp />, signUpTag)
}
if (recoveryTag) {
    render(<Recovery />, recoveryTag)
}