import React, { Component } from 'react'
const CircularJSON = require('circular-json');

const Context = React.createContext()

export class Provider extends Component {
    state = {
        products: localStorage.prod ? JSON.parse(localStorage.prod) : []
    }

    handleStates = (e, state, value) => {
        if (e) {
            e.preventDefault()
        }
        this.setState({ [state]: value })
        return false;
    }
    addProduct = (data) => {
        let { e } = data;
        if (e) {
            e.preventDefault()
        }
        delete data.e
        data.quantity = 1;
        let { products } = this.state;
        products.push(data);
        this.setState({ products: products })
        this.productsToLocalStorage()
    }
    incrementProduct = (e, id) => {
        e.preventDefault()
        let { products } = this.state;
        products[id].quantity += 1
        this.setState({ products: products })
    }
    deleteProduct = (e, id) => {
        e.preventDefault()
        let { products } = this.state
        products = products.filter((item, key) => {
            return key !== id
        })
        this.setState({ products: products }, () => {
            this.productsToLocalStorage()
        })
        return false;
    }
    productsToLocalStorage = (e) => {
        let { products } = this.state;
        let objProducts = {}
        objProducts.data = products
        localStorage.setItem('prod', CircularJSON.stringify(products))
    }

    render() {
        let store = {
            state: this.state,
            handleStates: this.handleStates,
            deleteProduct: this.deleteProduct,
            addProduct: this.addProduct
        }
        return (
            <Context.Provider value={store}>
                {this.props.children}
            </Context.Provider>
        )
    }
}

export const Consumer = Context.Consumer 