import React from 'react'
import { Consumer } from '../../Contexts/CartContext'

let Product = (props) => {
    let { name, price, image, id } = props;
    return (
        <Consumer>
            {store => {
                let { addProduct } = store
                let key = id + `_prod`
                return (
                    <div key={key} className="col-md-4 product-item">
                        <div className="product-shoe-info shoe">
                            <div className="men-pro-item">
                                <div className="men-thumb-item">
                                    <img className="product-image-home"
                                        src={image}
                                        alt="" />
                                    <div className="men-cart-pro">
                                        <div className="inner-men-cart-pro">
                                            <a href={'/product/' + id} className="link-product-add-cart">Quick View</a>
                                        </div>
                                    </div>
                                    <span className="product-new-top">New</span>
                                </div>
                                <div className="item-info-product">
                                    <h4>
                                        <a href={'/product/' + id}> {name} </a>
                                    </h4>
                                    <div className="info-product-price">
                                        <div className="grid_meta">
                                            <div className="product_price">
                                                <div className="grid-price ">
                                                    <span className="money ">R$ {price}</span>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="shoe single-item hvr-outline-out">
                                            <button className="btn btn-add" onClick={e => addProduct({ e, price, name, id, image })}>Add</button>
                                        </div>
                                    </div>
                                    <div className="clearfix"></div>
                                </div>
                            </div>
                        </div>
                    </div>

                )
            }}
        </Consumer>
    )
}
export default Product;