import React from 'react';
import { createPortal } from 'react-dom'
import ProductsList from './components/Home/ProductsList'
import Cart from './components/layout/Cart/Index'
import { Provider } from './Contexts/CartContext'

const CartPortal = (props) => (createPortal(<Cart />, document.getElementById('cart')))
const ProductsPortal = (props) => (createPortal(<ProductsList />, document.getElementById('products')))

let App = () => {
	return (
		<Provider>
			<div className="app">
				<CartPortal />
				<ProductsPortal />
			</div>
		</Provider>
	);
}

export default App;