import React, { Component } from 'react'
import axios from 'axios'
import { signUpUrl } from '../config'

//import context of react hooks  
const Context = React.createContext()

export class Provider extends Component {
    state = {
        message: '',
        isLoading: false,
    }
    handleStates = (e, state, value) => {
        if (e) {
            e.preventDefault()
        }
        this.setState({ [state]: value })
        return false;
    }
    handleInputs = (e) => {
        e.preventDefault()
        let { value, name } = e.target
        this.setState({ [name]: value })
    }
    handleSubmit = (e) => {
        e.preventDefault()
        this.setState({ isLoading: true })
        let data = this.state;
        let model = {
            nome: `${data.nome} ${data.sobrenome}`,
            email: data.email,
            cpf: data.cpf,
            rua: `${data.rua} ${data.numero} ${data.complemento}`,
            bairro: data.bairro,
            cidade: data.cidade,
            estado: data.estado,
            cep: data.cep,
            password: data.password
        }
        axios.post(signUpUrl, model)
            .then(response => {
                let { code, message } = response.data
                if (code === 200) {
                    localStorage.setItem('token', response.data.token)
                    setTimeout(() => {
                        window.location.url = response.data.url
                    }, 2000)
                }
                else {
                    this.setState({ message: message })
                }
                this.setState({ isLoading: !this.state.isLoading })
            })
            .catch(err => {
                console.log(err)
                this.setState({ message: 'Algo deu errado, por favor tente mais tarde' })
            })
        return false;
    }
    render() {
        const store = {
            state: this.state,
            handleStates: this.handleStates,
            handleInputs: this.handleInputs,
            handleSubmit: this.handleSubmit
        }
        return (
            <Context.Provider value={store}>
                {this.props.children}
            </Context.Provider>
        )
    }
}

export const Consumer = Context.Consumer 