import React, { Component } from 'react'
import { Email, Password } from './Layout'
import { Consumer } from '../../Contexts/LoginContext'
import Loading from '../layout/Loading'
import './index.css'

export default class Index extends Component {
    render() {
        return (
            <Consumer>
                {store => {
                    let { handleInputs, handleSubmit } = store
                    let { message, isLoading } = store.state
                    let loadingTag = isLoading ? <Loading message="Verificando..." /> : null
                    return (
                        <div className="login">
                            {loadingTag}
                            <div className="left">
                                MUSE
                            </div>
                            <div className="login-box right">
                                <h2> Login </h2>
                                <form className="form" onSubmit={handleSubmit}>
                                    <Email required={true} name='email' label='Email' handleInput={handleInputs} />
                                    <Password required={true} name='password' label='Senha' handleInput={handleInputs} />
                                    <div className="col-sm-12">
                                        <button>Entrar</button>
                                    </div>
                                    <div className="col-sm-12 danger">
                                        {message}
                                    </div>
                                    <div className="col-sm-12 options">
                                        <a href="/signup">Ainda não tenho cadastro</a>
                                        <a href="/forgot-password">Esqueci minha senha</a>
                                    </div>
                                </form>
                            </div>
                        </div>
                    )
                }}
            </Consumer>
        )
    }
}
