import React from 'react'
import { Email, InputCPF, Password, DateTimeText, InputPhone, Text, Select } from './Layout'
import Loading from '../layout/Loading'
import { Consumer } from '../../Contexts/SignUpContext'
import '../Auth/index.css'
import estados from './estados.json'

const Index = (props) => (
    <Consumer>
        {data => {
            let { handleSubmit } = data;
            let { isLoading, message } = data.state
            let danger = message ? 'col-sm-12 danger' : 'col-sm-12'
            let loading = isLoading ? <Loading message="Enviando seus dados" /> : null;
            return (
                <>
                    {loading}
                    <div className="login" >
                        <div className="left">MUSE</div>
                        <div className="login-box right">
                            <h2 className="form-title"> Sign Up </h2>
                            <form className="form" onSubmit={handleSubmit}>
                                <div className="row">
                                    <div className="col-sm-12"><h2> Informações pessoais </h2></div>
                                    <Text css="col-sm-6" name="nome" label="Nome:" handleInput={data.handleInputs} required={true} />
                                    <Text css="col-sm-6" name="sobrenome" label="Sobrenome:" handleInput={data.handleInputs} required={true} />
                                    <Email name="email" label="E-mail:" handleInput={data.handleInputs} required={true} />
                                    <InputCPF css="col-sm-6" name="cpf" label="CPF:" handleInput={data.handleInputs} required={true} />
                                    <DateTimeText css="col-sm-6" name="nascimento" label="Data de Nascimento:" handleInput={data.handleInputs} required={true} />
                                    <InputPhone css="col-sm-6" name="phone" label="Telefone:" handleInput={data.handleInputs} required={true} />
                                    <InputPhone css="col-sm-6" name="whatsapp" label="WhatsApp:" handleInput={data.handleInputs} />
                                    <div className="col-sm-12">
                                        <h2>Endereço</h2>
                                    </div>
                                    <Text css="col-sm-8" name="rua" label="Rua:" handleInput={data.handleInputs} required={true} />
                                    <Text css="col-sm-4" name="numero" label="Nº:" handleInput={data.handleInputs} required={true} />
                                    <Text css="col-sm-4" name="complemento" label="Complemento:" handleInput={data.handleInputs} />
                                    <Text css="col-sm-8 bairro" name="bairro" label="Bairro:" handleInput={data.handleInputs} required={true} />
                                    <Text css="col-sm-12 cidade" name="cidade" label="Cidade:" handleInput={data.handleInputs} required={true} />
                                    <Select css="col-sm-6 estado" options={estados} name="estado" label="Estado:" handleInput={data.handleInputs} required={true} />
                                    <Text css="col-sm-6 cep" name="cep" label="CEP:" handleInput={data.handleInputs} required={true} />
                                    <div className="col-md-12">
                                        <h2>Informações de Acesso</h2>
                                    </div>
                                    <Password name='password' label='Senha:' handleInput={data.handleInputs} required={true} />
                                    <Password name='confirm-password' label='Confirmar Senha:' handleInput={data.handleInputs} required={true} />
                                    <div className="col-sm-12">
                                        <button disabled={isLoading}>Entrar</button>
                                    </div>
                                    <div className={danger}>
                                        {message}
                                    </div>
                                    <div className="col-sm-12 options">
                                        ou
                                        <hr />
                                        <a href="/login" className="link"> Já sou cadastrado</a>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </>
            )
        }}
    </Consumer>
)

export default Index;