import React from 'react';
import { Consumer } from '../../../Contexts/CartContext'
import CartItem from './CartItem'
function makeid(length) {
	var result = '';
	var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
	var charactersLength = characters.length;
	for (var i = 0; i < length; i++) {
		result += characters.charAt(Math.floor(Math.random() * charactersLength));
	}
	return result;
}

let Index = (props) => {
	return (
		<Consumer>
			{store => {
				let { products } = store.state
				let { deleteProduct } = store;
				if (products.length) {
					return (
						products.map((item, key) => <CartItem deleteProduct={deleteProduct} key={makeid(4)} name={item.name} item_id={key} price={item.price} image={item.image} id={item.id} />)
					)
				}
				else {
					return 'Cart is empty'
				}
			}}
		</Consumer>
	)
}
export default Index