import React, { Component } from 'react'
import axios from 'axios'
import { Email } from './Layout'

export default class index extends Component {
    state = {
        message: '',
        disabled: true
    }
    handleSubmit = (e) => {
        e.preventDefault()
        let data = {}
        data.email = this.state.email
        axios.post('/recovery', { data })
            .then(response => {
                this.setState({ message: response })
            })
            .catch(err => {
                console.log(err)
                this.setState({ message: 'Ops! Estamos com algum problema nos nossos servidores, tente mais tarde' })
            })
        return false;
    }
    isEmail() {
        var re = /\S+@\S+\.\S+/;
        if (re.test(this.state.email)) {
            this.setState({ disabled: false })
        } else {
            this.setState({ disabled: true })
        }
    }
    handleChange = (e) => {
        e.preventDefault()
        let { value, name } = e.target
        this.setState({ [name]: value })
        this.isEmail()
    }
    render() {
        let { handleSubmit, handleChange } = this;
        return (
            <div className="login" >
                <div className="left">MUSE</div>
                <div className="login-box right">
                    <h2 className="form-title"> Recuperar senha </h2>
                    <form className="form" onSubmit={handleSubmit}>
                        <div className="row">
                            <Email name="email" label="E-mail:" handleInput={handleChange} required={true} />
                            <div className="col-sm-12">
                                <button disabled={this.state.disabled}>Recuperar</button>
                            </div>
                            <div className="col-sm-12 danger">
                                {this.state.message}
                            </div>

                            <div className="col-sm-12 options">
                                ou
                                <hr />
                                <a href="/login" className="link"> Já sou cadastrado</a>
                                <a href="/signup" className="link"> Quero me cadastrar </a>
                            </div>
                        </div>
                    </form>
                </div>
            </div>

        )
    }
}
