import React from 'react'
import InputMask from 'react-input-mask';

export const Text = (props) => {
    let { css, label, name, handleInput, required } = props;
    required = required ? true : false;
    css = css ? `${css} input-text_wrapper` : 'input-text_wrapper col-sm-12'
    return (
        <div className={css}>
            <label> {label} </label>
            <input type='text' name={name} onChange={handleInput} required={required} />
        </div>
    )
}
export const Select = (props) => {
    let { css, label, name, handleInput, required, options } = props;
    required = required ? true : false;
    options = options ? options : []
    css = css ? `${css} input-text_wrapper` : 'input-text_wrapper'
    return (
        <div className={css}>
            <label> {label} </label>
            <div className="select-wrapper form-group">
                <select name={name} className="form-control" onChange={handleInput} required={required}>
                    {options.map(item => (
                        <option value={item.sigla}> {item.sigla} </option>
                    ))}
                </select>
            </div>
        </div>
    )
}
