export const InputPhone = (props) => {
    let { css, label, name, handleInput, required } = props;
    css = css ? `${css} input-text_wrapper` : 'input-text_wrapper col-sm-12'
    return (
        <div className={css}>
            <label> {label} </label>
            <InputMask mask="(99) 99999-9999" name={name} type='tel' maskChar={null} onChange={handleInput} required={required} />
        </div>
    )
}
