import React, { Component } from 'react'
import axios from 'axios'
import Product from '../Products/Product'
export default class ProductsList extends Component {
    state = {
        products: []
    }
    componentDidMount() {
        let counter = 6;
        let url = `http://localhost:4003/products/api/${counter}`
        axios.get(url).then(response => {
            if (response.data) {
                this.setState({ data: response.data })
            } else {
                this.setState({
                    data: null,
                })
            }
        }).catch(err => {
            this.setState({ data: null })
        })
    }
    render() {
        let { data } = this.state
        return (
            <div>
                {data && data.map(item => {
                    return (
                        <Product key={item.id} name={item.nome} id={item.id} price={item.valorVenda} image={item.imagens} />
                    )
                })}
                {!data && <div> We got an error, try reload the page </div>}
            </div>
        )
    }
}
